@import "../global/variables";
@import "../global/mixins";

.social-list {
  @include list-style;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 22px 0 18px;
}

.social-list__item:first-child {
  margin: 0 37px 0 0;
}

.social-list__item:last-child {
  margin: 0 0 0 39px;
}

.social-list__link {
  color: #666666;

  &:active {
    opacity: 0.3;
  }
}

.social-list__link-image {
  fill: #666666;
}

.social-list__link:hover {
  color: $green;
}

@media (min-width: $tablet-width) {
  .social-list {
    width: 124px;
    justify-content: space-between;
    padding: 11px 0 0;
    margin-left: 17px;
  }

  .social-list__item:first-child,
  .social-list__item:last-child {
    margin: 0;
  }

  .social-list__item--vk .social-list__link-image {
    width: 30px;
    height: 16px;
  }

  .social-list__item--inst .social-list__link-image {
    width: 23px;
    height: 23px;
  }

  .social-list__item--fb .social-list__link-image {
    width: 14px;
    height: 28px;
  }
}
